.profile__title {
  color: #fff;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  margin: 0 0 8px;
  padding: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media (max-width: 425px) {
  .profile__title {
    font-size: 27px;
    line-height: 33px;
  }
}